<template>
  <div class="content">
    <div
      class="hobbies_box"
      v-for="(i,index) in list"
      :key="index"
      :style="`animation-delay:${index*0.2}s;`"
    >
      <div class="l_iocn">
        <i :class="'iconfont ' + i.icon" v-if="i.icon"></i>
        <span class="title" v-else>简</span>
      </div>
      <div class="r_content">
        <p>{{i.text}}</p>
      </div>
    </div>
    <text-bottom :block="true"></text-bottom>
  </div>
</template>

<script>
import TextBottom from "../common/TextBottom";
export default {
  components: {
    "text-bottom": TextBottom
  },
  data() {
    return {
      list: [
        {
          title: "简",
          text: "写简书"
        },
        {
          icon: "icongithub",
          text:
            "哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
        },
        {
          icon: "iconyinle",
          text: "听音乐"
        },
        {
          icon: "icondianying",
          text: "哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
        },
        {
          icon: "iconSkiing",
          text:
            "哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
        },
        {
          icon: "iconzuqiu-copy",
          text:
            "哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
        },
        {
          icon: "iconpingpang",
          text:
            "哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
        },
        {
          icon: "iconyumaoqiu",
          text:
            "哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
        },
        {
          icon: "iconyouxi",
          text:
            "哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
.content {
  padding: 1rem;
}
.hobbies_box {
  margin-top: 1rem;
  padding: 0.5rem;
  background: white;
  transition: 0.3s;
  border: 2px solid transparent;
  box-shadow: 0px 0px 3px #ccc;
  display: flex;
  animation: move 0.5s ease both 1;
  &:hover {
    border: 2px solid var(--base);
    box-shadow: 2px 2px 3px #ccc inset;
  }
  .l_iocn {
    min-width: 3rem;
    //   background: red;
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      font-size: 2rem;
      color: var(--base);
    }
    .title {
      font-size: 1.5rem;
      color: var(--base);
    }
  }
  .r_content {
    padding-left: 1rem;
    font-size: 0.75rem;
    color: #5a5a5a;
  }
}
@keyframes move {
  0% {
    opacity: 0;
    transform: translateY(2rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}
</style>


